import React, { useRef } from "react";
import { useSelector } from "react-redux";
import SpreadSheet from "../../../app/common/form/SpreadSheet";
import columnsOptions from "./columnsOptions";

export default function MemberHubCostSalesList({ refresh }) {
  const { memberHubs } = useSelector((state) => state.memberHub);
  const { memberRanks } = useSelector((state) => state.memberRank);
  const { memberHubCostSaless } = useSelector(
    (state) => state.memberHubCostSales
  );

  const options = useRef({
    columns: columnsOptions(memberHubs, memberRanks),
  });

  return <SpreadSheet options={options} data={memberHubCostSaless} />;
}
