import cuid from "cuid";
import lo from "lodash";
import firebase from "../config/firebase";

const db = firebase.firestore();
const COLLECTION_NAME = "acc_subject";

export function fetchAccSubjectsFromFirestore(yyyyMM) {
  return db
    .collection(COLLECTION_NAME)
    .where("closing_year_month", "==", yyyyMM)
    .orderBy("created_at", "asc");
}

export function loadFromAccSubjectFromFirestore(accSubjectId) {
  return db.collection(COLLECTION_NAME).doc(accSubjectId);
}

export function addAccSubjectToFirestore(accSubject) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.FieldValue.serverTimestamp();
  return db
    .collection(COLLECTION_NAME)
    .doc(accSubject.closing_year_month + "-" + cuid())
    .set({
      ...accSubject,
      created_at: now,
      created_user: user.email,
      updated_at: now,
      updated_user: user.email,
    });
}

export function updateAccSubjectToFirestore(accSubject) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.FieldValue.serverTimestamp();
  return db
    .collection(COLLECTION_NAME)
    .doc(accSubject.id)
    .update({
      ...lo.omit(accSubject, "id"),
      updated_at: now,
      updated_user: user.email,
    });
}
