import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, Redirect, useHistory } from "react-router-dom";
import { Button, Menu } from "semantic-ui-react";
import { toast } from "react-toastify";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import MyTextInput from "../../../app/common/form/MyTextInput";
import MySelectInput from "../../../app/common/form/MySelectInput";
import { availableOptions } from "../../../app/options/availableOptions";
import NowLoading from "../../../app/common/utils/NowLoading";
import useFirestoreDoc from "../../../app/hooks/useFirestoreDoc";
import { loadFromProjectFromFirestore } from "../../../app/firestore/projectService";
import { createProject, selectProject, updateProject } from "./projectReducer";
import { clientOptionsFactory } from "../../../app/options/clientOptionsFactory";
import { accDepartmentOptionsFactory } from "../../../app/options/accDepartmentOptionsFactory";
import { accSubjectOptionsFactory } from "../../../app/options/accSubjectOptionsFactory";

export default function ProjectForm({
  projectId,
  setVisibleDetail,
  handleRefresh,
}) {
  const { clients } = useSelector((state) => state.client);
  const { accDepartments } = useSelector((state) => state.accDepartment);
  const { accSubjects } = useSelector((state) => state.accSubject);
  const { currentPeriod } = useSelector((state) => state.period);
  const { selectedProject } = useSelector((state) => state.project);
  const { loading, error } = useSelector((state) => state.async);
  const dispatch = useDispatch();
  const history = useHistory();

  const initialValues = selectedProject ?? {
    closing_year_month: currentPeriod.yearMonth,
    name: "",
    client: "",
    accDepartment: "",
    accSubject: "",
    note: "",
    available: "true",
  };

  const validationSchema = Yup.object({
    name: Yup.string().required("案件名称を入力してください"),
    client: Yup.string().required("取引先を設定してください"),
    accDepartment: Yup.string().required("計上部門を設定してください"),
    accSubject: Yup.string().notRequired("費用科目"),
    note: Yup.string().notRequired(),
    available: Yup.string().required("有効・無効を設定してください"),
  });

  useFirestoreDoc({
    shouldExecute: !!projectId,
    query: () => loadFromProjectFromFirestore(projectId),
    data: (item) => dispatch(selectProject(item)),
    deps: [projectId, dispatch],
  });

  if (loading) {
    return <NowLoading />;
  }

  if (error) {
    return <Redirect to="/error" />;
  }

  return (
    <>
      <Menu secondary className="jmenu">
        <Menu.Item className={"secondary_toolbar_title"}>案件</Menu.Item>
      </Menu>
      <Formik
        enableReinitialize={true}
        validateOnBlur={false}
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={async (values, { setSubmitting }) => {
          try {
            setSubmitting(true);
            projectId
              ? dispatch(updateProject(values))
              : dispatch(createProject(values));
            setVisibleDetail(false);
            history.push("/projects");
            handleRefresh();
          } catch (error) {
            toast.error(error.message);
          } finally {
            setSubmitting(false);
          }
        }}>
        {({ isSubmitting, dirty, isValid, resetForm, setFieldValue }) => (
          <Form className="ui form">
            <input type="hidden" name="id" />
            <input type="hidden" name="closing_year_month" />
            <MyTextInput label="案件名称" name="name" placeholder="案件名称" />
            <MySelectInput
              label="取引先"
              name="client"
              options={clientOptionsFactory(clients)}
              search
            />
            <MySelectInput
              label="計上部門"
              name="accDepartment"
              options={accDepartmentOptionsFactory(accDepartments)}
            />
            <MySelectInput
              label="費用科目"
              name="accSubject"
              options={accSubjectOptionsFactory(accSubjects)}
            />
            <MyTextInput label="備考" name="note" placeholder="備考" />
            <MySelectInput
              label="有効・無効"
              name="available"
              options={availableOptions}
            />

            <Button
              loading={isSubmitting}
              disabled={!isValid || !dirty || isSubmitting}
              type="submit"
              positive
              content="保存"
            />
            <Button
              negative
              content="キャンセル"
              as={Link}
              to="/projects"
              onClick={() => {
                setVisibleDetail(false);
                resetForm();
              }}
            />
          </Form>
        )}
      </Formik>
    </>
  );
}
