import {
  asyncActionError,
  asyncActionFinish,
  asyncActionStart,
} from "../../../app/async/asyncReducer";
import { dataFromSnapshot } from "../../../app/firestore/firestoreService";
import {
  addMemberHubCostSalesToFirestore,
  fetchMemberHubCostSalessFromFirestore,
  updateMemberHubCostSalesToFirestore,
} from "../../../app/firestore/memberHubCostSalesService";

export const MEMBERHUB_COST_SALES_CREATE = "memberHubCostSales/create";
export const MEMBERHUB_COST_SALES_UPDATE = "memberHubCostSales/update";
export const MEMBERHUB_COST_SALESS_FETCH = "memberHubCostSales/fetch";
export const MEMBERHUB_COST_SALES_LISTEN = "memberHubCostSales/listen";
export const MEMBERHUB_COST_SALES_SELECTED = "memberHubCostSales/selected";

export function selectMemberHubCostSales(memberHubCostSales) {
  return {
    type: MEMBERHUB_COST_SALES_SELECTED,
    payload: memberHubCostSales,
  };
}

export function createMemberHubCostSales(memberHubCostSales) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await addMemberHubCostSalesToFirestore(memberHubCostSales);
      dispatch({
        type: MEMBERHUB_COST_SALES_CREATE,
        payload: memberHubCostSales,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function updateMemberHubCostSales(memberHubCostSales) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await updateMemberHubCostSalesToFirestore(memberHubCostSales);
      dispatch({
        type: MEMBERHUB_COST_SALES_UPDATE,
        payload: memberHubCostSales,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function fetchMemberHubCostSaless(yyyyMM) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      const snapshot = fetchMemberHubCostSalessFromFirestore(yyyyMM).get();
      const data = (await snapshot).docs.map((doc) => {
        const ret = dataFromSnapshot(doc);
        const href = `<a href="#${ret.id}">編集</a>`;
        return { href, ...ret };
      });
      dispatch({
        type: MEMBERHUB_COST_SALESS_FETCH,
        payload: data,
      });
      dispatch(asyncActionFinish());
      return data;
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

const initialState = {
  memberHubCostSaless: [],
  selectedMemberHubCostSales: null,
};

export default function memberHubCostSalesReducer(
  state = initialState,
  { type, payload }
) {
  switch (type) {
    case MEMBERHUB_COST_SALES_SELECTED:
      return {
        ...state,
        selectedMemberHubCostSales: payload,
      };
    case MEMBERHUB_COST_SALES_CREATE:
      return state;
    case MEMBERHUB_COST_SALES_UPDATE:
      return state;
    case MEMBERHUB_COST_SALESS_FETCH:
      return {
        ...state,
        memberHubCostSaless: payload,
      };
    case MEMBERHUB_COST_SALES_LISTEN:
      return {
        ...state,
        selectedMemberHubCostSales: payload,
      };
    default:
      return state;
  }
}
