import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, Redirect, useHistory } from "react-router-dom";
import { Button, Menu } from "semantic-ui-react";
import { toast } from "react-toastify";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import MyTextInput from "../../../app/common/form/MyTextInput";
import MySelectInput from "../../../app/common/form/MySelectInput";
import { availableOptions } from "../../../app/options/availableOptions";
import NowLoading from "../../../app/common/utils/NowLoading";
import useFirestoreDoc from "../../../app/hooks/useFirestoreDoc";
import { loadFromMemberHubCostSalesFromFirestore } from "../../../app/firestore/memberHubCostSalesService";
import {
  createMemberHubCostSales,
  selectMemberHubCostSales,
  updateMemberHubCostSales,
} from "./memberHubCostSalesReducer";
import { memberHubOptionsFactory } from "../../../app/options/memberHubOptionsFactory";
import { hubCostSalesKindOptions } from "../../../app/options/hubCostSalesKindOptions";
import { memberRankOptionsFactory } from "../../../app/options/memberRankOptionsFactory";

export default function MemberHubCostSalesForm({
  memberHubCostSalesId,
  setVisibleDetail,
  handleRefresh,
}) {
  const { memberHubs } = useSelector((state) => state.memberHub);
  const { memberRanks } = useSelector((state) => state.memberRank);
  const { currentPeriod } = useSelector((state) => state.period);
  const { selectedMemberHubCostSales } = useSelector(
    (state) => state.memberHubCostSales
  );
  const { loading, error } = useSelector((state) => state.async);
  const dispatch = useDispatch();
  const history = useHistory();

  const initialValues = selectedMemberHubCostSales ?? {
    closing_year_month: currentPeriod.yearMonth,
    cost_sales_kind: "",
    hub: "",
    rank: "",
    cost_sales: 0,
    available: "true",
  };

  const validationSchema = Yup.object({
    cost_sales_kind: Yup.string().required("売上・原価区分を設定してください"),
    hub: Yup.string().required("拠点を設定してください"),
    rank: Yup.string().required("ランクを設定してください"),
    cost_sales: Yup.number()
      .required("金額を設定してください")
      .typeError("数値を入力してください"),
    available: Yup.string().required("有効・無効を設定してください"),
  });

  useFirestoreDoc({
    shouldExecute: !!memberHubCostSalesId,
    query: () => loadFromMemberHubCostSalesFromFirestore(memberHubCostSalesId),
    data: (item) => dispatch(selectMemberHubCostSales(item)),
    deps: [memberHubCostSalesId, dispatch],
  });

  if (loading) {
    return <NowLoading />;
  }

  if (error) {
    return <Redirect to="/error" />;
  }

  return (
    <>
      <Menu secondary className="jmenu">
        <Menu.Item className={"secondary_toolbar_title"}>
          メンバー拠点別標準原価売上表
        </Menu.Item>
      </Menu>
      <Formik
        enableReinitialize={true}
        validateOnBlur={false}
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={async (values, { setSubmitting }) => {
          try {
            setSubmitting(true);
            memberHubCostSalesId
              ? dispatch(updateMemberHubCostSales(values))
              : dispatch(createMemberHubCostSales(values));
            setVisibleDetail(false);
            history.push("/memberhubcostsaless");
            handleRefresh();
          } catch (error) {
            toast.error(error.message);
          } finally {
            setSubmitting(false);
          }
        }}>
        {({ isSubmitting, dirty, isValid, resetForm }) => (
          <Form className="ui form">
            <input type="hidden" name="id" />
            <input type="hidden" name="closing_year_month" />
            <MySelectInput
              label="売上・原価区分"
              name="cost_sales_kind"
              options={hubCostSalesKindOptions}
            />
            <MySelectInput
              label="拠点"
              name="hub"
              options={memberHubOptionsFactory(memberHubs)}
            />
            <MySelectInput
              label="ランク"
              name="rank"
              options={memberRankOptionsFactory(memberRanks)}
            />
            <MyTextInput label="金額" name="price" placeholder="金額" />
            <MySelectInput
              label="有効・無効"
              name="available"
              options={availableOptions}
            />

            <Button
              loading={isSubmitting}
              disabled={!isValid || !dirty || isSubmitting}
              type="submit"
              positive
              content="保存"
            />
            <Button
              negative
              content="キャンセル"
              as={Link}
              to="/memberhubcostsaless"
              onClick={() => {
                setVisibleDetail(false);
                resetForm();
              }}
            />
          </Form>
        )}
      </Formik>
    </>
  );
}
