import {
  asyncActionError,
  asyncActionFinish,
  asyncActionStart,
} from "../../../app/async/asyncReducer";
import { dataFromSnapshot } from "../../../app/firestore/firestoreService";
import {
  addProjectToFirestore,
  fetchProjectsFromFirestore,
  updateProjectToFirestore,
} from "../../../app/firestore/projectService";

export const PROJECT_CREATE = "project/create";
export const PROJECT_UPDATE = "project/update";
export const PROJECTS_FETCH = "project/fetch";
export const PROJECT_LISTEN = "project/listen";
export const PROJECT_SELECTED = "project/selected";

export function selectProject(project) {
  return {
    type: PROJECT_SELECTED,
    payload: project,
  };
}

export function createProject(project) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await addProjectToFirestore(project);
      dispatch({
        type: PROJECT_CREATE,
        payload: project,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function updateProject(project) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await updateProjectToFirestore(project);
      dispatch({
        type: PROJECT_UPDATE,
        payload: project,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function fetchProjects(yyyyMM) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      const snapshot = fetchProjectsFromFirestore(yyyyMM).get();
      const data = (await snapshot).docs.map((doc) => {
        const ret = dataFromSnapshot(doc);
        const href = `<a href="#${ret.id}">編集</a>`;
        return { href, ...ret };
      });
      dispatch({
        type: PROJECTS_FETCH,
        payload: data,
      });
      dispatch(asyncActionFinish());
      return data;
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

const initialState = {
  projects: [],
  selectedProject: null,
};

export default function projectReducer(
  state = initialState,
  { type, payload }
) {
  switch (type) {
    case PROJECT_SELECTED:
      return {
        ...state,
        selectedProject: payload,
      };
    case PROJECT_CREATE:
      return state;
    case PROJECT_UPDATE:
      return state;
    case PROJECTS_FETCH:
      return {
        ...state,
        projects: payload,
      };
    case PROJECT_LISTEN:
      return {
        ...state,
        selectedProject: payload,
      };
    default:
      return state;
  }
}
