import React, { useRef } from "react";
import { useSelector } from "react-redux";
import SpreadSheet from "../../../app/common/form/SpreadSheet";
import columnsOptions from "./columnsOptions";

export default function AccDepartmentList({ refresh }) {
  const { accSubjects } = useSelector((state) => state.accSubject);

  const options = useRef({
    columns: columnsOptions(),
  });

  return <SpreadSheet options={options} data={accSubjects} />;
}
