import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, Redirect, useHistory } from "react-router-dom";
import { Button, Menu } from "semantic-ui-react";
import { toast } from "react-toastify";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import MyTextInput from "../../../app/common/form/MyTextInput";
import MySelectInput from "../../../app/common/form/MySelectInput";
import { availableOptions } from "../../../app/options/availableOptions";
import NowLoading from "../../../app/common/utils/NowLoading";
import useFirestoreDoc from "../../../app/hooks/useFirestoreDoc";
import { loadFromMemberFromFirestore } from "../../../app/firestore/memberService";
import { createMember, selectMember, updateMember } from "./memberReducer";
import { memberHubOptionsFactory } from "../../../app/options/memberHubOptionsFactory";
import { memberRankOptionsFactory } from "../../../app/options/memberRankOptionsFactory";
import { memberAffiliationOptionsFactory } from "../../../app/options/memberAffiliationOptionsFactory";
import { memberTagOptionsFactory } from "../../../app/options/memberTagOptionsFactory";

export default function MemberForm({
  memberId,
  setVisibleDetail,
  handleRefresh,
}) {
  const { memberTags } = useSelector((state) => state.memberTag);
  const { memberAffiliations } = useSelector(
    (state) => state.memberAffiliation
  );
  const { memberHubs } = useSelector((state) => state.memberHub);
  const { memberRanks } = useSelector((state) => state.memberRank);
  const { currentPeriod } = useSelector((state) => state.period);
  const { selectedMember } = useSelector((state) => state.member);
  const { loading, error } = useSelector((state) => state.async);
  const [useCostSales, setUseCostSales] = useState(false);
  const dispatch = useDispatch();
  const history = useHistory();

  const initialValues = selectedMember ?? {
    closing_year_month: currentPeriod.yearMonth,
    name: "",
    tag: "",
    affiliation: "",
    hub: "",
    rank: "",
    available: "true",
  };

  const validationSchema = Yup.object({
    name: Yup.string().required("氏名を入力してください"),
    tag: Yup.string().required("メンバータグを設定してください"),
    affiliation: Yup.string().required("メンバー所属を設定してください"),
    hub: Yup.string().notRequired(),
    rank: Yup.string().required("メンバーランクを設定してください"),
    available: Yup.string().required("有効・無効を設定してください"),
  });

  const isUseCostSales = (value) => {
    return (
      !memberTags.filter(
        (memberTag) =>
          memberTag.id === value && memberTag.use_cost_sales === "true"
      ).length > 0
    );
  };

  useFirestoreDoc({
    shouldExecute: !!memberId,
    query: () => loadFromMemberFromFirestore(memberId),
    data: (item) => {
      dispatch(selectMember(item));
      setUseCostSales(isUseCostSales(item?.tag));
    },
    deps: [memberId, dispatch],
  });

  if (loading) {
    return <NowLoading />;
  }

  if (error) {
    return <Redirect to="/error" />;
  }

  return (
    <>
      <Menu secondary className="jmenu">
        <Menu.Item className={"secondary_toolbar_title"}>メンバー</Menu.Item>
      </Menu>
      <Formik
        enableReinitialize={true}
        validateOnBlur={false}
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={async (values, { setSubmitting }) => {
          try {
            setSubmitting(true);
            memberId
              ? dispatch(updateMember(values))
              : dispatch(createMember(values));
            setVisibleDetail(false);
            history.push("/members");
            handleRefresh();
          } catch (error) {
            toast.error(error.message);
          } finally {
            setSubmitting(false);
          }
        }}>
        {({ isSubmitting, dirty, isValid, resetForm, setFieldValue }) => (
          <Form className="ui form">
            <input type="hidden" name="id" />
            <input type="hidden" name="closing_year_month" />
            <MyTextInput label="氏名" name="name" placeholder="氏名" />
            <MySelectInput
              label="メンバータグ"
              name="tag"
              options={memberTagOptionsFactory(memberTags)}
              onChange={(e) => {
                setFieldValue("tag", e.target.id);
                if (useCostSales === false) {
                  setFieldValue("hub", "");
                }
                setUseCostSales(isUseCostSales(e.target.id));
              }}
            />
            <MySelectInput
              label="メンバー所属"
              name="affiliation"
              options={memberAffiliationOptionsFactory(memberAffiliations)}
              search
            />
            <MySelectInput
              label="メンバー拠点"
              name="hub"
              options={memberHubOptionsFactory(memberHubs)}
              clearable
              disabled={useCostSales}
            />
            <MySelectInput
              label="メンバーランク"
              name="rank"
              options={memberRankOptionsFactory(memberRanks)}
            />
            <MySelectInput
              label="有効・無効"
              name="available"
              options={availableOptions}
            />

            <Button
              loading={isSubmitting}
              disabled={!isValid || !dirty || isSubmitting}
              type="submit"
              positive
              content="保存"
            />
            <Button
              negative
              content="キャンセル"
              as={Link}
              to="/members"
              onClick={() => {
                setVisibleDetail(false);
                resetForm();
              }}
            />
          </Form>
        )}
      </Formik>
    </>
  );
}
