import { availableOptions } from "../../../app/options/availableOptions";
import { memberAffiliationOptionsFactory } from "../../../app/options/memberAffiliationOptionsFactory";
import { memberHubOptionsFactory } from "../../../app/options/memberHubOptionsFactory";
import { memberRankOptionsFactory } from "../../../app/options/memberRankOptionsFactory";
import { memberTagOptionsFactory } from "../../../app/options/memberTagOptionsFactory";

const columnsOptions = (
  memberTags,
  memberAffiliations,
  memberHubs,
  memberRanks
) => {
  return [
    {
      name: "href",
      title: "リンク",
      type: "html",
      width: "100px",
      readOnly: true,
      align: "center",
    },
    {
      name: "name",
      title: "氏名",
      type: "text",
      width: "200px",
      readOnly: true,
    },
    {
      name: "tag",
      title: "メンバータグ",
      type: "dropdown",
      source: memberTagOptionsFactory(memberTags),
      width: "140px",
      readOnly: true,
    },
    {
      name: "affiliation",
      title: "メンバー所属",
      type: "dropdown",
      source: memberAffiliationOptionsFactory(memberAffiliations),
      width: "250px",
      readOnly: true,
    },
    {
      name: "hub",
      title: "メンバー拠点",
      type: "dropdown",
      source: memberHubOptionsFactory(memberHubs),
      width: "140px",
      readOnly: true,
    },
    {
      name: "rank",
      title: "メンバーランク",
      type: "dropdown",
      source: memberRankOptionsFactory(memberRanks),
      width: "140px",
      readOnly: true,
    },
    {
      name: "available",
      title: "有効・無効",
      type: "dropdown",
      source: availableOptions,
      width: "140px",
      readOnly: true,
      align: "center",
    },
    {
      name: "created_at",
      title: "作成日",
      type: "hidden",
      width: "0",
      readOnly: true,
    },
    {
      name: "created_user",
      title: "作成ユーザー",
      type: "hidden",
      width: "0",
      readOnly: true,
    },
    {
      name: "updated_at",
      title: "更新日",
      type: "hidden",
      width: "0",
      readOnly: true,
    },
    {
      name: "updated_user",
      title: "更新ユーザー",
      type: "hidden",
      width: "0",
      readOnly: true,
    },
    {
      name: "id",
      title: "ドキュメントID",
      type: "hidden",
      width: "0",
      readOnly: true,
    },
    {
      name: "closing_year_month",
      title: "締め年月",
      type: "hidden",
      width: "0",
      readOnly: true,
    },
  ];
};

export default columnsOptions;
