import cuid from "cuid";
import lo from "lodash";
import firebase from "../config/firebase";

const db = firebase.firestore();
const COLLECTION_NAME = "member_hub";

export function fetchMemberHubsFromFirestore(yyyyMM) {
  return db
    .collection(COLLECTION_NAME)
    .where("closing_year_month", "==", yyyyMM)
    .orderBy("created_at", "asc");
}

export function loadFromMemberHubFromFirestore(memberHubId) {
  return db.collection(COLLECTION_NAME).doc(memberHubId);
}

export function addMemberHubToFirestore(memberHub) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.FieldValue.serverTimestamp();
  return db
    .collection(COLLECTION_NAME)
    .doc(memberHub.closing_year_month + "-" + cuid())
    .set({
      ...memberHub,
      created_at: now,
      created_user: user.email,
      updated_at: now,
      updated_user: user.email,
    });
}

export function updateMemberHubToFirestore(memberHub) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.FieldValue.serverTimestamp();
  return db
    .collection(COLLECTION_NAME)
    .doc(memberHub.id)
    .update({
      ...lo.omit(memberHub, "id"),
      updated_at: now,
      updated_user: user.email,
    });
}
