import cuid from "cuid";
import lo from "lodash";
import firebase from "../config/firebase";

const db = firebase.firestore();
const COLLECTION_NAME = "member";

export function fetchMembersFromFirestore(yyyyMM) {
  return db
    .collection(COLLECTION_NAME)
    .where("closing_year_month", "==", yyyyMM)
    .orderBy("created_at", "asc");
}

export function loadFromMemberFromFirestore(memberId) {
  return db.collection(COLLECTION_NAME).doc(memberId);
}

export function addMemberToFirestore(member) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.FieldValue.serverTimestamp();
  return db
    .collection(COLLECTION_NAME)
    .doc(member.closing_year_month + "-" + cuid())
    .set({
      ...member,
      created_at: now,
      created_user: user.email,
      updated_at: now,
      updated_user: user.email,
    });
}

export function updateMemberToFirestore(member) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.FieldValue.serverTimestamp();
  return db
    .collection(COLLECTION_NAME)
    .doc(member.id)
    .update({
      ...lo.omit(member, "id"),
      updated_at: now,
      updated_user: user.email,
    });
}
