import {
  asyncActionError,
  asyncActionFinish,
  asyncActionStart,
} from "../../../app/async/asyncReducer";
import { dataFromSnapshot } from "../../../app/firestore/firestoreService";
import {
  addMemberRankToFirestore,
  fetchMemberRanksFromFirestore,
  updateMemberRankToFirestore,
} from "../../../app/firestore/memberRankService";

export const MEMBERRANK_CREATE = "memberRank/create";
export const MEMBERRANK_UPDATE = "memberRank/update";
export const MEMBERRANKS_FETCH = "memberRank/fetch";
export const MEMBERRANK_LISTEN = "memberRank/listen";
export const MEMBERRANK_SELECTED = "memberRank/selected";

export function selectMemberRank(memberRank) {
  return {
    type: MEMBERRANK_SELECTED,
    payload: memberRank,
  };
}

export function createMemberRank(memberRank) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await addMemberRankToFirestore(memberRank);
      dispatch({
        type: MEMBERRANK_CREATE,
        payload: memberRank,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function updateMemberRank(memberRank) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await updateMemberRankToFirestore(memberRank);
      dispatch({
        type: MEMBERRANK_UPDATE,
        payload: memberRank,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function fetchMemberRanks(yyyyMM) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      const snapshot = fetchMemberRanksFromFirestore(yyyyMM).get();
      const data = (await snapshot).docs.map((doc) => {
        const ret = dataFromSnapshot(doc);
        const href = `<a href="#${ret.id}">編集</a>`;
        return { href, ...ret };
      });
      dispatch({
        type: MEMBERRANKS_FETCH,
        payload: data,
      });
      dispatch(asyncActionFinish());
      return data;
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

const initialState = {
  memberRanks: [],
  selectedMemberRank: null,
};

export default function memberRankReducer(
  state = initialState,
  { type, payload }
) {
  switch (type) {
    case MEMBERRANK_SELECTED:
      return {
        ...state,
        selectedMemberRank: payload,
      };
    case MEMBERRANK_CREATE:
      return state;
    case MEMBERRANK_UPDATE:
      return state;
    case MEMBERRANKS_FETCH:
      return {
        ...state,
        memberRanks: payload,
      };
    case MEMBERRANK_LISTEN:
      return {
        ...state,
        selectedMemberRank: payload,
      };
    default:
      return state;
  }
}
