import cuid from "cuid";
import lo from "lodash";
import firebase from "../config/firebase";

const db = firebase.firestore();
const COLLECTION_NAME = "member_rank";

export function fetchMemberRanksFromFirestore(yyyyMM) {
  return db
    .collection(COLLECTION_NAME)
    .where("closing_year_month", "==", yyyyMM)
    .orderBy("created_at", "asc");
}

export function loadFromMemberRankFromFirestore(memberRankId) {
  return db.collection(COLLECTION_NAME).doc(memberRankId);
}

export function addMemberRankToFirestore(memberRank) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.FieldValue.serverTimestamp();
  return db
    .collection(COLLECTION_NAME)
    .doc(memberRank.closing_year_month + "-" + cuid())
    .set({
      ...memberRank,
      created_at: now,
      created_user: user.email,
      updated_at: now,
      updated_user: user.email,
    });
}

export function updateMemberRankToFirestore(memberRank) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.FieldValue.serverTimestamp();
  return db
    .collection(COLLECTION_NAME)
    .doc(memberRank.id)
    .update({
      ...lo.omit(memberRank, "id"),
      updated_at: now,
      updated_user: user.email,
    });
}
