import {
  asyncActionError,
  asyncActionFinish,
  asyncActionStart,
} from "../../../app/async/asyncReducer";
import { dataFromSnapshot } from "../../../app/firestore/firestoreService";
import {
  addClientToFirestore,
  fetchClientsFromFirestore,
  updateClientToFirestore,
} from "../../../app/firestore/clientService";

export const CLIENT_CREATE = "client/create";
export const CLIENT_UPDATE = "client/update";
export const CLIENTS_FETCH = "client/fetch";
export const CLIENT_LISTEN = "client/listen";
export const CLIENT_SELECTED = "client/selected";

export function selectClient(client) {
  return {
    type: CLIENT_SELECTED,
    payload: client,
  };
}

export function createClient(client) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await addClientToFirestore(client);
      dispatch({
        type: CLIENT_CREATE,
        payload: client,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function updateClient(client) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await updateClientToFirestore(client);
      dispatch({
        type: CLIENT_UPDATE,
        payload: client,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function fetchClients(yyyyMM) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      const snapshot = fetchClientsFromFirestore(yyyyMM).get();
      const data = (await snapshot).docs.map((doc) => {
        const ret = dataFromSnapshot(doc);
        const href = `<a href="#${ret.id}">編集</a>`;
        return { href, ...ret };
      });
      dispatch({
        type: CLIENTS_FETCH,
        payload: data,
      });
      dispatch(asyncActionFinish());
      return data;
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

const initialState = {
  clients: [],
  selectedClient: null,
};

export default function clientReducer(state = initialState, { type, payload }) {
  switch (type) {
    case CLIENT_SELECTED:
      return {
        ...state,
        selectedClient: payload,
      };
    case CLIENT_CREATE:
      return state;
    case CLIENT_UPDATE:
      return state;
    case CLIENTS_FETCH:
      return {
        ...state,
        clients: payload,
      };
    case CLIENT_LISTEN:
      return {
        ...state,
        selectedClient: payload,
      };
    default:
      return state;
  }
}
