import React from "react";
import { useSelector } from "react-redux";
import { Menu } from "semantic-ui-react";
import { displayYYYYMMJp } from "../../app/common/utils/textFormstUtils";

export default function PeriodMonthMenu() {
  const { currentPeriod, periods } = useSelector((state) => state.period);

  return (
    <>
      {periods.length !== 0 && (
        <Menu.Item>{displayYYYYMMJp(currentPeriod.yearMonth)}</Menu.Item>
      )}
    </>
  );
}
