import {
  asyncActionError,
  asyncActionFinish,
  asyncActionStart,
} from "../../../app/async/asyncReducer";
import { dataFromSnapshot } from "../../../app/firestore/firestoreService";
import {
  addMemberTagToFirestore,
  fetchMemberTagsFromFirestore,
  updateMemberTagToFirestore,
} from "../../../app/firestore/memberTagService";

export const MEMBERTAG_CREATE = "memberTag/create";
export const MEMBERTAG_UPDATE = "memberTag/update";
export const MEMBERTAGS_FETCH = "memberTag/fetch";
export const MEMBERTAG_LISTEN = "memberTag/listen";
export const MEMBERTAG_SELECTED = "memberTag/selected";

export function selectMemberTag(memberTag) {
  return {
    type: MEMBERTAG_SELECTED,
    payload: memberTag,
  };
}

export function createMemberTag(memberTag) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await addMemberTagToFirestore(memberTag);
      dispatch({
        type: MEMBERTAG_CREATE,
        payload: memberTag,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function updateMemberTag(memberTag) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await updateMemberTagToFirestore(memberTag);
      dispatch({
        type: MEMBERTAG_UPDATE,
        payload: memberTag,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function fetchMemberTags(yyyyMM) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      const snapshot = fetchMemberTagsFromFirestore(yyyyMM).get();
      const data = (await snapshot).docs.map((doc) => {
        const ret = dataFromSnapshot(doc);
        const href = `<a href="#${ret.id}">編集</a>`;
        return { href, ...ret };
      });
      dispatch({
        type: MEMBERTAGS_FETCH,
        payload: data,
      });
      dispatch(asyncActionFinish());
      return data;
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

const initialState = {
  memberTags: [],
  selectedMemberTag: null,
};

export default function memberTagReducer(
  state = initialState,
  { type, payload }
) {
  switch (type) {
    case MEMBERTAG_SELECTED:
      return {
        ...state,
        selectedMemberTag: payload,
      };
    case MEMBERTAG_CREATE:
      return state;
    case MEMBERTAG_UPDATE:
      return state;
    case MEMBERTAGS_FETCH:
      return {
        ...state,
        memberTags: payload,
      };
    case MEMBERTAG_LISTEN:
      return {
        ...state,
        selectedMemberTag: payload,
      };
    default:
      return state;
  }
}
