import React, { useState } from "react";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Redirect } from "react-router-dom";
import { Icon, Menu, Sidebar } from "semantic-ui-react";
import NowLoading from "../../../app/common/utils/NowLoading";
import { fetchMemberAffiliations } from "../memberAffiliations/memberAffiliationReducer";
import { fetchMemberHubs } from "../memberHubs/memberHubReducer";
import { fetchMemberRanks } from "../memberRanks/memberRankReducer";
import { fetchMemberTags } from "../memberTags/memberTagReducer";
import MemberForm from "./MemberForm";
import MemberList from "./MemberList";
import { fetchMembers } from "./memberReducer";

export default function MemberDashboard({ location }) {
  const memberId = location.hash?.replace("#", "");
  const [visibleDetail, setVisibleDetail] = useState(false);
  const { currentPeriod } = useSelector((state) => state.period);
  const dispatch = useDispatch();
  const [refresh, setRefresh] = useState(0);
  const { loading, error } = useSelector((state) => state.async);

  useEffect(() => {
    if (memberId) {
      setVisibleDetail(true);
    }
  }, [memberId]);

  useEffect(() => {
    dispatch(fetchMemberTags(currentPeriod.yearMonth));
    dispatch(fetchMemberAffiliations(currentPeriod.yearMonth));
    dispatch(fetchMemberHubs(currentPeriod.yearMonth));
    dispatch(fetchMemberRanks(currentPeriod.yearMonth));
    dispatch(fetchMembers(currentPeriod.yearMonth));
  }, [currentPeriod.yearMonth, dispatch, refresh]);

  const handleRefresh = () => {
    setRefresh(refresh + 1);
  };

  if (loading && !visibleDetail) {
    return <NowLoading />;
  }

  if (error) {
    return <Redirect to="/error" />;
  }

  return (
    <>
      <Sidebar.Pushable>
        <Sidebar
          width="very wide"
          as={Menu}
          animation="overlay"
          direction="right"
          vertical
          visible={visibleDetail}>
          <Menu.Item>
            <MemberForm
              memberId={memberId}
              setVisibleDetail={setVisibleDetail}
              handleRefresh={handleRefresh}
            />
          </Menu.Item>
        </Sidebar>

        <Sidebar.Pusher className="jpusher" dimmed={visibleDetail}>
          <Menu secondary className="jmenu">
            <Menu.Item className={"secondary_toolbar_title"}>
              メンバー
            </Menu.Item>
            <Menu.Item
              onClick={() => {
                handleRefresh();
              }}>
              <Icon name="refresh" />
              最新の状態に更新
            </Menu.Item>
            <Menu.Item onClick={() => setVisibleDetail(!visibleDetail)}>
              <Icon name="edit" />
              新しいレコード
            </Menu.Item>
          </Menu>
          <MemberList refresh={refresh} />
        </Sidebar.Pusher>
      </Sidebar.Pushable>
    </>
  );
}
