import React from "react";
import { useHistory } from "react-router-dom";
import { Button, Label } from "semantic-ui-react";
import { Form, Formik } from "formik";
import * as Yup from "yup";
import MyTextInput from "../../app/common/form/MyTextInput";
import { signInFirebaseWithEmail } from "../../app/firebase/authService";
import ModalWrapper from "../../app/common/modals/ModalWrapper";

export default function LoginForm() {
  const history = useHistory();

  return (
    <ModalWrapper size="mini" header="フォーキャストにサインイン">
      <Formik
        onSubmit={async (values, { setSubmitting, setErrors }) => {
          setSubmitting(true);
          try {
            await signInFirebaseWithEmail(values);
            history.push("/dashboard");
          } catch (error) {
            setErrors({
              auth: "メールアドレス、またはパスワードが間違っています",
            });
          } finally {
            setSubmitting(false);
          }
        }}
        initialValues={{ email: "", password: "" }}
        validationSchema={Yup.object({
          email: Yup.string()
            .required("Email Address を入力してください")
            .email("Email 形式で入力してください"),
          password: Yup.string().required("パスワードを入力してください"),
        })}>
        {({ isSubmitting, dirty, isValid, errors }) => (
          <Form className="ui form">
            <MyTextInput name="email" placeholder="Email Address" />
            <MyTextInput
              name="password"
              placeholder="password"
              type="password"
            />
            {errors.auth && (
              <Label
                basic
                color="red"
                style={{ marginBottom: 10 }}
                content={errors.auth}
              />
            )}

            <Button
              loading={isSubmitting}
              disabled={!isValid || !dirty || isSubmitting}
              type="submit"
              fluid
              size="large"
              color="teal"
              content="サインイン"
            />
          </Form>
        )}
      </Formik>
    </ModalWrapper>
  );
}
