import {
  asyncActionError,
  asyncActionFinish,
  asyncActionStart,
} from "../../../app/async/asyncReducer";
import { dataFromSnapshot } from "../../../app/firestore/firestoreService";
import {
  addAccSubjectToFirestore,
  fetchAccSubjectsFromFirestore,
  updateAccSubjectToFirestore,
} from "../../../app/firestore/accSubjectService";

export const ACC_SUBJECT_CREATE = "accSubject/create";
export const ACC_SUBJECT_UPDATE = "accSubject/update";
export const ACC_SUBJECTS_FETCH = "accSubject/fetch";
export const ACC_SUBJECT_LISTEN = "accSubject/listen";
export const ACC_SUBJECT_SELECTED = "accSubject/selected";

export function selectAccSubject(accSubject) {
  return {
    type: ACC_SUBJECT_SELECTED,
    payload: accSubject,
  };
}

export function createAccSubject(accSubject) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await addAccSubjectToFirestore(accSubject);
      dispatch({
        type: ACC_SUBJECT_CREATE,
        payload: accSubject,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function updateAccSubject(accSubject) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await updateAccSubjectToFirestore(accSubject);
      dispatch({
        type: ACC_SUBJECT_UPDATE,
        payload: accSubject,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function fetchAccSubjects(yyyyMM) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      const snapshot = fetchAccSubjectsFromFirestore(yyyyMM).get();
      const data = (await snapshot).docs.map((doc) => {
        const ret = dataFromSnapshot(doc);
        const href = `<a href="#${ret.id}">編集</a>`;
        return { href, ...ret };
      });
      dispatch({
        type: ACC_SUBJECTS_FETCH,
        payload: data,
      });
      dispatch(asyncActionFinish());
      return data;
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

const initialState = {
  accSubjects: [],
  selectedAccSubject: null,
};

export default function accSubjectReducer(
  state = initialState,
  { type, payload }
) {
  switch (type) {
    case ACC_SUBJECT_SELECTED:
      return {
        ...state,
        selectedAccSubject: payload,
      };
    case ACC_SUBJECT_CREATE:
      return state;
    case ACC_SUBJECT_UPDATE:
      return state;
    case ACC_SUBJECTS_FETCH:
      return {
        ...state,
        accSubjects: payload,
      };
    case ACC_SUBJECT_LISTEN:
      return {
        ...state,
        selectedAccSubject: payload,
      };
    default:
      return state;
  }
}
