import React, { useRef } from "react";
import { useSelector } from "react-redux";
import SpreadSheet from "../../../app/common/form/SpreadSheet";
import columnsOptions from "./columnsOptions";

export default function MemberAffiliationList({ refresh }) {
  const { memberTags } = useSelector((state) => state.memberTag);
  const { memberAffiliations } = useSelector(
    (state) => state.memberAffiliation
  );

  const options = useRef({
    columns: columnsOptions(memberTags),
  });

  return <SpreadSheet options={options} data={memberAffiliations} />;
}
