export const memberTagOptionsFactory = (
  memberTags,
  available = "true",
  use_cost_sales = ["true", "false"]
) => {
  return memberTags
    .filter(
      (tag) =>
        tag.available === available &&
        use_cost_sales.includes(tag.use_cost_sales)
    )
    .map((tag) => ({
      key: tag.id,
      id: tag.id,
      text: tag.name,
      name: tag.name,
      value: tag.id,
    }));
};
