import React from "react";
import { Route } from "react-router-dom";
import { useSelector } from "react-redux";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.min.css";
import { Container } from "semantic-ui-react";
import ModalManager from "../common/modals/ModalManager";
import NowLoading from "../common/utils/NowLoading";
import ErrorComponent from "../common/errors/ErrorComponent";
import AuthContainer from "../../features/auth/AuthContainer";
import HomePage from "../../features/home/HomePage";
import NavBar from "../../features/nav/NavBar";
import Dashboard from "../../features/dashboard/Dashboard";
import ProfilePage from "../../features/profiles/profilePage/ProfilePage";
import AssignPage from "../../features/assign/AssignPage";
import ClientDashboard from "../../features/master/clients/ClientDashboard";
import MemberDashboard from "../../features/master/members/MemberDashboard";
import ProjectDashboard from "../../features/master/projects/ProjectDashboard";
import MemberTagDashboard from "../../features/master/memberTags/MemberTagDashboard";
import MemberRankDashboard from "../../features/master/memberRanks/MemberRankDashboard";
import MemberAffiliationDashboard from "../../features/master/memberAffiliations/MemberAffiliationDashboard";
import MemberHubDashboard from "../../features/master/memberHubs/MemberHubDashboard";
import MemberHubCostSalesDashboard from "../../features/master/memberHubCostSaless/MemberHubCostSalesDashboard";
import AccDepartmentDashboard from "../../features/master/accDepartments/AccDepartmentDashboard";
import AccSubjectDashboard from "../../features/master/accSubjects/AccSubjectDashboard";

function App() {
  const { initialized } = useSelector((state) => state.async);

  if (!initialized) {
    return <NowLoading content="Loading Application..." />;
  }

  return (
    <>
      <ModalManager />
      <ToastContainer position="bottom-right" hideProgressBar />
      <Route exact path="/" component={HomePage} />
      <Route
        path={"/(.+)"}
        render={() => (
          <AuthContainer>
            <NavBar />
            <Container className="wide">
              <Route exact path="/error" component={ErrorComponent} />
              <Route exact path="/dashboard" component={Dashboard} />
              <Route path="/assign/:month" component={AssignPage} />
              <Route exact path="/profile" component={ProfilePage} />
              <Route exact path="/clients" component={ClientDashboard} />
              <Route exact path="/members" component={MemberDashboard} />
              <Route exact path="/projects" component={ProjectDashboard} />
              <Route exact path="/membertags" component={MemberTagDashboard} />
              <Route
                exact
                path="/memberranks"
                component={MemberRankDashboard}
              />
              <Route
                exact
                path="/memberaffiliations"
                component={MemberAffiliationDashboard}
              />
              <Route exact path="/memberhubs" component={MemberHubDashboard} />
              <Route
                exact
                path="/memberhubcostsaless"
                component={MemberHubCostSalesDashboard}
              />
              <Route
                exact
                path="/accdepartments"
                component={AccDepartmentDashboard}
              />
              <Route
                exact
                path="/accsubjects"
                component={AccSubjectDashboard}
              />
            </Container>
          </AuthContainer>
        )}
      />
    </>
  );
}

export default App;
