import cuid from "cuid";
import lo from "lodash";
import firebase from "../config/firebase";

const db = firebase.firestore();
const COLLECTION_NAME = "member_hub_cost_sales";

export function fetchMemberHubCostSalessFromFirestore(yyyyMM) {
  return db
    .collection(COLLECTION_NAME)
    .where("closing_year_month", "==", yyyyMM)
    .orderBy("created_at", "asc");
}

export function loadFromMemberHubCostSalesFromFirestore(memberHubCostSalesId) {
  return db.collection(COLLECTION_NAME).doc(memberHubCostSalesId);
}

export function addMemberHubCostSalesToFirestore(memberHubCostSales) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.FieldValue.serverTimestamp();
  return db
    .collection(COLLECTION_NAME)
    .doc(memberHubCostSales.closing_year_month + "-" + cuid())
    .set({
      ...memberHubCostSales,
      created_at: now,
      created_user: user.email,
      updated_at: now,
      updated_user: user.email,
    });
}

export function updateMemberHubCostSalesToFirestore(memberHubCostSales) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.FieldValue.serverTimestamp();
  return db
    .collection(COLLECTION_NAME)
    .doc(memberHubCostSales.id)
    .update({
      ...lo.omit(memberHubCostSales, "id"),
      updated_at: now,
      updated_user: user.email,
    });
}
