import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, Redirect, useHistory } from "react-router-dom";
import { Button, Menu } from "semantic-ui-react";
import { toast } from "react-toastify";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import MyTextInput from "../../../app/common/form/MyTextInput";
import MySelectInput from "../../../app/common/form/MySelectInput";
import { availableOptions } from "../../../app/options/availableOptions";
import NowLoading from "../../../app/common/utils/NowLoading";
import useFirestoreDoc from "../../../app/hooks/useFirestoreDoc";
import { loadFromAccDepartmentFromFirestore } from "../../../app/firestore/accDepartmentService";
import {
  createAccDepartment,
  selectAccDepartment,
  updateAccDepartment,
} from "./accDepartmentReducer";

export default function AccDepartmentForm({
  accDepartmentId,
  setVisibleDetail,
  handleRefresh,
}) {
  const { currentPeriod } = useSelector((state) => state.period);
  const { selectedAccDepartment } = useSelector((state) => state.accDepartment);
  const { loading, error } = useSelector((state) => state.async);
  const dispatch = useDispatch();
  const history = useHistory();

  const initialValues = selectedAccDepartment ?? {
    closing_year_month: currentPeriod.yearMonth,
    name: "",
    available: "true",
  };

  const validationSchema = Yup.object({
    name: Yup.string().required("計上部門名称を入力してください"),
    available: Yup.string().required("有効・無効を設定してください"),
  });

  useFirestoreDoc({
    shouldExecute: !!accDepartmentId,
    query: () => loadFromAccDepartmentFromFirestore(accDepartmentId),
    data: (item) => dispatch(selectAccDepartment(item)),
    deps: [accDepartmentId, dispatch],
  });

  if (loading) {
    return <NowLoading />;
  }

  if (error) {
    return <Redirect to="/error" />;
  }

  return (
    <>
      <Menu secondary className="jmenu">
        <Menu.Item className={"secondary_toolbar_title"}>計上部門</Menu.Item>
      </Menu>
      <Formik
        enableReinitialize={true}
        validateOnBlur={false}
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={async (values, { setSubmitting }) => {
          try {
            setSubmitting(true);
            accDepartmentId
              ? dispatch(updateAccDepartment(values))
              : dispatch(createAccDepartment(values));
            setVisibleDetail(false);
            history.push("/accdepartments");
            handleRefresh();
          } catch (error) {
            toast.error(error.message);
          } finally {
            setSubmitting(false);
          }
        }}>
        {({ isSubmitting, dirty, isValid, resetForm }) => (
          <Form className="ui form">
            <input type="hidden" name="id" />
            <input type="hidden" name="closing_year_month" />
            <MyTextInput label="計上部門名称" name="name" placeholder="名称" />
            <MySelectInput
              label="有効・無効"
              name="available"
              options={availableOptions}
            />

            <Button
              loading={isSubmitting}
              disabled={!isValid || !dirty || isSubmitting}
              type="submit"
              positive
              content="保存"
            />
            <Button
              negative
              content="キャンセル"
              as={Link}
              to="/accdepartments"
              onClick={() => {
                setVisibleDetail(false);
                resetForm();
              }}
            />
          </Form>
        )}
      </Formik>
    </>
  );
}
