import { combineReducers } from "redux";
import authReducer from "../../features/auth/authReducer";
import accDepartmentReducer from "../../features/master/accDepartments/accDepartmentReducer";
import accSubjectReducer from "../../features/master/accSubjects/accSubjectReducer";
import clientReducer from "../../features/master/clients/clientReducer";
import memberAffiliationReducer from "../../features/master/memberAffiliations/memberAffiliationReducer";
import memberHubCostSalesReducer from "../../features/master/memberHubCostSaless/memberHubCostSalesReducer";
import memberHubReducer from "../../features/master/memberHubs/memberHubReducer";
import memberRankReducer from "../../features/master/memberRanks/memberRankReducer";
import memberReducer from "../../features/master/members/memberReducer";
import memberTagReducer from "../../features/master/memberTags/memberTagReducer";
import projectReducer from "../../features/master/projects/projectReducer";
import periodReducer from "../../features/period/periodReducer";
import asyncReducer from "../async/asyncReducer";
import modalReducer from "../common/modals/modalReducer";

const rootReducer = combineReducers({
  modals: modalReducer,
  auth: authReducer,
  async: asyncReducer,
  period: periodReducer,
  member: memberReducer,
  memberTag: memberTagReducer,
  memberRank: memberRankReducer,
  memberAffiliation: memberAffiliationReducer,
  memberHub: memberHubReducer,
  memberHubCostSales: memberHubCostSalesReducer,
  accDepartment: accDepartmentReducer,
  accSubject: accSubjectReducer,
  client: clientReducer,
  project: projectReducer,
});

export default rootReducer;
