import React, { useRef } from "react";
import { useSelector } from "react-redux";
import SpreadSheet from "../../../app/common/form/SpreadSheet";
import columnsOptions from "./columnsOptions";

export default function MemberList({ refresh }) {
  const { memberTags } = useSelector((state) => state.memberTag);
  const { memberAffiliations } = useSelector(
    (state) => state.memberAffiliation
  );
  const { memberHubs } = useSelector((state) => state.memberHub);
  const { memberRanks } = useSelector((state) => state.memberRank);
  const { members } = useSelector((state) => state.member);

  const options = useRef({
    columns: columnsOptions(
      memberTags,
      memberAffiliations,
      memberHubs,
      memberRanks
    ),
  });

  return <SpreadSheet options={options} data={members} />;
}
