export const accDepartmentOptionsFactory = (
  accDepartments,
  available = "true"
) => {
  return accDepartments
    .filter((accDepartment) => accDepartment.available === available)
    .map((accDepartment) => ({
      key: accDepartment.id,
      id: accDepartment.id,
      text: accDepartment.name,
      name: accDepartment.name,
      value: accDepartment.id,
    }));
};
