import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, Redirect, useHistory } from "react-router-dom";
import { Button, Menu } from "semantic-ui-react";
import { toast } from "react-toastify";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import MyTextInput from "../../../app/common/form/MyTextInput";
import MySelectInput from "../../../app/common/form/MySelectInput";
import { availableOptions } from "../../../app/options/availableOptions";
import NowLoading from "../../../app/common/utils/NowLoading";
import useFirestoreDoc from "../../../app/hooks/useFirestoreDoc";
import { loadFromAccSubjectFromFirestore } from "../../../app/firestore/accSubjectService";
import {
  createAccSubject,
  selectAccSubject,
  updateAccSubject,
} from "./accSubjectReducer";
import { canInputSalesOptions } from "../../../app/options/canInputSalesOptions";

export default function AccSubjectForm({
  accSubjectId,
  setVisibleDetail,
  handleRefresh,
}) {
  const { currentPeriod } = useSelector((state) => state.period);
  const { selectedAccSubject } = useSelector((state) => state.accSubject);
  const { loading, error } = useSelector((state) => state.async);
  const dispatch = useDispatch();
  const history = useHistory();

  const initialValues = selectedAccSubject ?? {
    closing_year_month: currentPeriod.yearMonth,
    name: "",
    can_input_sales: "true",
    available: "true",
  };

  const validationSchema = Yup.object({
    name: Yup.string().required("費用科目名称を入力してください"),
    can_input_sales: Yup.string().required("売上入力可否を設定してください"),
    available: Yup.string().required("有効・無効を設定してください"),
  });

  useFirestoreDoc({
    shouldExecute: !!accSubjectId,
    query: () => loadFromAccSubjectFromFirestore(accSubjectId),
    data: (item) => dispatch(selectAccSubject(item)),
    deps: [accSubjectId, dispatch],
  });

  if (loading) {
    return <NowLoading />;
  }

  if (error) {
    return <Redirect to="/error" />;
  }

  return (
    <>
      <Menu secondary className="jmenu">
        <Menu.Item className={"secondary_toolbar_title"}>費用科目</Menu.Item>
      </Menu>
      <Formik
        enableReinitialize={true}
        validateOnBlur={false}
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={async (values, { setSubmitting }) => {
          try {
            setSubmitting(true);
            accSubjectId
              ? dispatch(updateAccSubject(values))
              : dispatch(createAccSubject(values));
            setVisibleDetail(false);
            history.push("/accsubjects");
            handleRefresh();
          } catch (error) {
            toast.error(error.message);
          } finally {
            setSubmitting(false);
          }
        }}>
        {({ isSubmitting, dirty, isValid, resetForm }) => (
          <Form className="ui form">
            <input type="hidden" name="id" />
            <input type="hidden" name="closing_year_month" />
            <MyTextInput label="費用科目名称" name="name" placeholder="名称" />
            <MySelectInput
              label="売上入力可否"
              name="can_input_sales"
              options={canInputSalesOptions}
            />
            <MySelectInput
              label="有効・無効"
              name="available"
              options={availableOptions}
            />

            <Button
              loading={isSubmitting}
              disabled={!isValid || !dirty || isSubmitting}
              type="submit"
              positive
              content="保存"
            />
            <Button
              negative
              content="キャンセル"
              as={Link}
              to="/accsubjects"
              onClick={() => {
                setVisibleDetail(false);
                resetForm();
              }}
            />
          </Form>
        )}
      </Formik>
    </>
  );
}
