import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, Redirect, useHistory } from "react-router-dom";
import { Button, Menu } from "semantic-ui-react";
import { toast } from "react-toastify";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import MyTextInput from "../../../app/common/form/MyTextInput";
import MySelectInput from "../../../app/common/form/MySelectInput";
import { availableOptions } from "../../../app/options/availableOptions";
import NowLoading from "../../../app/common/utils/NowLoading";
import useFirestoreDoc from "../../../app/hooks/useFirestoreDoc";
import { loadFromMemberHubFromFirestore } from "../../../app/firestore/memberHubService";
import {
  createMemberHub,
  selectMemberHub,
  updateMemberHub,
} from "./memberHubReducer";
import { memberTagOptionsFactory } from "../../../app/options/memberTagOptionsFactory";

export default function MemberHubForm({
  memberHubId,
  setVisibleDetail,
  handleRefresh,
}) {
  const { memberTags } = useSelector((state) => state.memberTag);
  const { currentPeriod } = useSelector((state) => state.period);
  const { selectedMemberHub } = useSelector((state) => state.memberHub);
  const { loading, error } = useSelector((state) => state.async);
  const dispatch = useDispatch();
  const history = useHistory();

  const initialValues = selectedMemberHub ?? {
    closing_year_month: currentPeriod.yearMonth,
    name: "",
    tag: "true",
    available: "true",
  };

  const validationSchema = Yup.object({
    name: Yup.string().required("拠点名称を入力してください"),
    tag: Yup.string().required("メンバータグを設定してください"),
    available: Yup.string().required("有効・無効を設定してください"),
  });

  useFirestoreDoc({
    shouldExecute: !!memberHubId,
    query: () => loadFromMemberHubFromFirestore(memberHubId),
    data: (item) => dispatch(selectMemberHub(item)),
    deps: [memberHubId, dispatch],
  });

  if (loading) {
    return <NowLoading />;
  }

  if (error) {
    return <Redirect to="/error" />;
  }

  return (
    <>
      <Menu secondary className="jmenu">
        <Menu.Item className={"secondary_toolbar_title"}>
          メンバー拠点
        </Menu.Item>
      </Menu>
      <Formik
        enableReinitialize={true}
        validateOnBlur={false}
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={async (values, { setSubmitting }) => {
          try {
            setSubmitting(true);
            memberHubId
              ? dispatch(updateMemberHub(values))
              : dispatch(createMemberHub(values));
            setVisibleDetail(false);
            history.push("/memberhubs");
            handleRefresh();
          } catch (error) {
            toast.error(error.message);
          } finally {
            setSubmitting(false);
          }
        }}>
        {({ isSubmitting, dirty, isValid, resetForm }) => (
          <Form className="ui form">
            <input type="hidden" name="id" />
            <input type="hidden" name="closing_year_month" />
            <MyTextInput label="拠点名称" name="name" placeholder="拠点名称" />
            <MySelectInput
              label="メンバータグ"
              name="tag"
              options={memberTagOptionsFactory(memberTags, "true", ["true"])}
            />
            <MySelectInput
              label="有効・無効"
              name="available"
              options={availableOptions}
            />

            <Button
              loading={isSubmitting}
              disabled={!isValid || !dirty || isSubmitting}
              type="submit"
              positive
              content="保存"
            />
            <Button
              negative
              content="キャンセル"
              as={Link}
              to="/memberhubs"
              onClick={() => {
                setVisibleDetail(false);
                resetForm();
              }}
            />
          </Form>
        )}
      </Formik>
    </>
  );
}
