import {
  asyncActionError,
  asyncActionFinish,
  asyncActionStart,
} from "../../../app/async/asyncReducer";
import { dataFromSnapshot } from "../../../app/firestore/firestoreService";
import {
  addMemberHubToFirestore,
  fetchMemberHubsFromFirestore,
  updateMemberHubToFirestore,
} from "../../../app/firestore/memberHubService";

export const MEMBERHUB_CREATE = "memberHub/create";
export const MEMBERHUB_UPDATE = "memberHub/update";
export const MEMBERHUBS_FETCH = "memberHub/fetch";
export const MEMBERHUB_LISTEN = "memberHub/listen";
export const MEMBERHUB_SELECTED = "memberHub/selected";

export function selectMemberHub(memberHub) {
  return {
    type: MEMBERHUB_SELECTED,
    payload: memberHub,
  };
}

export function createMemberHub(memberHub) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await addMemberHubToFirestore(memberHub);
      dispatch({
        type: MEMBERHUB_CREATE,
        payload: memberHub,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function updateMemberHub(memberHub) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await updateMemberHubToFirestore(memberHub);
      dispatch({
        type: MEMBERHUB_UPDATE,
        payload: memberHub,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function fetchMemberHubs(yyyyMM) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      const snapshot = fetchMemberHubsFromFirestore(yyyyMM).get();
      const data = (await snapshot).docs.map((doc) => {
        const ret = dataFromSnapshot(doc);
        const href = `<a href="#${ret.id}">編集</a>`;
        return { href, ...ret };
      });
      dispatch({
        type: MEMBERHUBS_FETCH,
        payload: data,
      });
      dispatch(asyncActionFinish());
      return data;
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

const initialState = {
  memberHubs: [],
  selectedMemberHub: null,
};

export default function memberHubReducer(
  state = initialState,
  { type, payload }
) {
  switch (type) {
    case MEMBERHUB_SELECTED:
      return {
        ...state,
        selectedMemberHub: payload,
      };
    case MEMBERHUB_CREATE:
      return state;
    case MEMBERHUB_UPDATE:
      return state;
    case MEMBERHUBS_FETCH:
      return {
        ...state,
        memberHubs: payload,
      };
    case MEMBERHUB_LISTEN:
      return {
        ...state,
        selectedMemberHub: payload,
      };
    default:
      return state;
  }
}
