import {
  asyncActionError,
  asyncActionFinish,
  asyncActionStart,
} from "../../../app/async/asyncReducer";
import { dataFromSnapshot } from "../../../app/firestore/firestoreService";
import {
  addMemberToFirestore,
  fetchMembersFromFirestore,
  updateMemberToFirestore,
} from "../../../app/firestore/memberService";

export const MEMBER_CREATE = "member/create";
export const MEMBER_UPDATE = "member/update";
export const MEMBERS_FETCH = "member/fetch";
export const MEMBER_LISTEN = "member/listen";
export const MEMBER_SELECTED = "member/selected";

export function selectMember(member) {
  return {
    type: MEMBER_SELECTED,
    payload: member,
  };
}

export function createMember(member) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await addMemberToFirestore(member);
      dispatch({
        type: MEMBER_CREATE,
        payload: member,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function updateMember(member) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await updateMemberToFirestore(member);
      dispatch({
        type: MEMBER_UPDATE,
        payload: member,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function fetchMembers(yyyyMM) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      const snapshot = fetchMembersFromFirestore(yyyyMM).get();
      const data = (await snapshot).docs.map((doc) => {
        const ret = dataFromSnapshot(doc);
        const href = `<a href="#${ret.id}">編集</a>`;
        return { href, ...ret };
      });
      dispatch({
        type: MEMBERS_FETCH,
        payload: data,
      });
      dispatch(asyncActionFinish());
      return data;
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

const initialState = {
  members: [],
  selectedMember: null,
};

export default function memberReducer(state = initialState, { type, payload }) {
  switch (type) {
    case MEMBER_SELECTED:
      return {
        ...state,
        selectedMember: payload,
      };
    case MEMBER_CREATE:
      return state;
    case MEMBER_UPDATE:
      return state;
    case MEMBERS_FETCH:
      return {
        ...state,
        members: payload,
      };
    case MEMBER_LISTEN:
      return {
        ...state,
        selectedMember: payload,
      };
    default:
      return state;
  }
}
