import React, { useState } from "react";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Redirect } from "react-router-dom";
import { Icon, Menu, Sidebar } from "semantic-ui-react";
import NowLoading from "../../../app/common/utils/NowLoading";
import { fetchAccDepartments } from "../accDepartments/accDepartmentReducer";
import { fetchAccSubjects } from "../accSubjects/accSubjectReducer";
import { fetchClients } from "../clients/clientReducer";
import ProjectForm from "./ProjectForm";
import ProjectList from "./ProjectList";
import { fetchProjects } from "./projectReducer";

export default function ProjectDashboard({ location }) {
  const projectId = location.hash?.replace("#", "");
  const [visibleDetail, setVisibleDetail] = useState(false);
  const { currentPeriod } = useSelector((state) => state.period);
  const dispatch = useDispatch();
  const [refresh, setRefresh] = useState(0);
  const { loading, error } = useSelector((state) => state.async);

  useEffect(() => {
    if (projectId) {
      setVisibleDetail(true);
    }
  }, [projectId]);

  useEffect(() => {
    dispatch(fetchClients(currentPeriod.yearMonth));
    dispatch(fetchAccDepartments(currentPeriod.yearMonth));
    dispatch(fetchAccSubjects(currentPeriod.yearMonth));
    dispatch(fetchProjects(currentPeriod.yearMonth));
  }, [currentPeriod.yearMonth, dispatch, refresh]);

  const handleRefresh = () => {
    setRefresh(refresh + 1);
  };

  if (loading && !visibleDetail) {
    return <NowLoading />;
  }

  if (error) {
    return <Redirect to="/error" />;
  }

  return (
    <>
      <Sidebar.Pushable>
        <Sidebar
          width="very wide"
          as={Menu}
          animation="overlay"
          direction="right"
          vertical
          visible={visibleDetail}>
          <Menu.Item>
            <ProjectForm
              projectId={projectId}
              setVisibleDetail={setVisibleDetail}
              handleRefresh={handleRefresh}
            />
          </Menu.Item>
        </Sidebar>

        <Sidebar.Pusher className="jpusher" dimmed={visibleDetail}>
          <Menu secondary className="jmenu">
            <Menu.Item className={"secondary_toolbar_title"}>案件</Menu.Item>
            <Menu.Item
              onClick={() => {
                handleRefresh();
              }}>
              <Icon name="refresh" />
              最新の状態に更新
            </Menu.Item>
            <Menu.Item onClick={() => setVisibleDetail(!visibleDetail)}>
              <Icon name="edit" />
              新しいレコード
            </Menu.Item>
          </Menu>
          <ProjectList refresh={refresh} />
        </Sidebar.Pusher>
      </Sidebar.Pushable>
    </>
  );
}
