import {
  asyncActionError,
  asyncActionFinish,
  asyncActionStart,
} from "../../../app/async/asyncReducer";
import { dataFromSnapshot } from "../../../app/firestore/firestoreService";
import {
  addAccDepartmentToFirestore,
  fetchAccDepartmentsFromFirestore,
  updateAccDepartmentToFirestore,
} from "../../../app/firestore/accDepartmentService";

export const ACC_DEPARTMENT_CREATE = "accDepartment/create";
export const ACC_DEPARTMENT_UPDATE = "accDepartment/update";
export const ACC_DEPARTMENTS_FETCH = "accDepartment/fetch";
export const ACC_DEPARTMENT_LISTEN = "accDepartment/listen";
export const ACC_DEPARTMENT_SELECTED = "accDepartment/selected";

export function selectAccDepartment(accDepartment) {
  return {
    type: ACC_DEPARTMENT_SELECTED,
    payload: accDepartment,
  };
}

export function createAccDepartment(accDepartment) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await addAccDepartmentToFirestore(accDepartment);
      dispatch({
        type: ACC_DEPARTMENT_CREATE,
        payload: accDepartment,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function updateAccDepartment(accDepartment) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await updateAccDepartmentToFirestore(accDepartment);
      dispatch({
        type: ACC_DEPARTMENT_UPDATE,
        payload: accDepartment,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function fetchAccDepartments(yyyyMM) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      const snapshot = fetchAccDepartmentsFromFirestore(yyyyMM).get();
      const data = (await snapshot).docs.map((doc) => {
        const ret = dataFromSnapshot(doc);
        const href = `<a href="#${ret.id}">編集</a>`;
        return { href, ...ret };
      });
      dispatch({
        type: ACC_DEPARTMENTS_FETCH,
        payload: data,
      });
      dispatch(asyncActionFinish());
      return data;
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

const initialState = {
  accDepartments: [],
  selectedAccDepartment: null,
};

export default function accDepartmentReducer(
  state = initialState,
  { type, payload }
) {
  switch (type) {
    case ACC_DEPARTMENT_SELECTED:
      return {
        ...state,
        selectedAccDepartment: payload,
      };
    case ACC_DEPARTMENT_CREATE:
      return state;
    case ACC_DEPARTMENT_UPDATE:
      return state;
    case ACC_DEPARTMENTS_FETCH:
      return {
        ...state,
        accDepartments: payload,
      };
    case ACC_DEPARTMENT_LISTEN:
      return {
        ...state,
        selectedAccDepartment: payload,
      };
    default:
      return state;
  }
}
