import cuid from "cuid";
import lo from "lodash";
import firebase from "../config/firebase";

const db = firebase.firestore();
const COLLECTION_NAME = "member_affiliation";

export function fetchMemberAffiliationsFromFirestore(yyyyMM) {
  return db
    .collection(COLLECTION_NAME)
    .where("closing_year_month", "==", yyyyMM)
    .orderBy("created_at", "asc");
}

export function loadFromMemberAffiliationFromFirestore(memberAffiliationId) {
  return db.collection(COLLECTION_NAME).doc(memberAffiliationId);
}

export function addMemberAffiliationToFirestore(memberAffiliation) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.FieldValue.serverTimestamp();
  return db
    .collection(COLLECTION_NAME)
    .doc(memberAffiliation.closing_year_month + "-" + cuid())
    .set({
      ...memberAffiliation,
      created_at: now,
      created_user: user.email,
      updated_at: now,
      updated_user: user.email,
    });
}

export function updateMemberAffiliationToFirestore(memberAffiliation) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.FieldValue.serverTimestamp();
  return db
    .collection(COLLECTION_NAME)
    .doc(memberAffiliation.id)
    .update({
      ...lo.omit(memberAffiliation, "id"),
      updated_at: now,
      updated_user: user.email,
    });
}
