export const accSubjectOptionsFactory = (accSubjects, available = "true") => {
  return accSubjects
    .filter((accSubject) => accSubject.available === available)
    .map((accSubject) => ({
      key: accSubject.id,
      id: accSubject.id,
      text: accSubject.name,
      name: accSubject.name,
      value: accSubject.id,
    }));
};
