import { accDepartmentOptionsFactory } from "../../../app/options/accDepartmentOptionsFactory";
import { accSubjectOptionsFactory } from "../../../app/options/accSubjectOptionsFactory";
import { availableOptions } from "../../../app/options/availableOptions";
import { clientOptionsFactory } from "../../../app/options/clientOptionsFactory";

const columnsOptions = (clients, accDepartments, accSubjects) => {
  return [
    {
      name: "href",
      title: "リンク",
      type: "html",
      width: "100px",
      readOnly: true,
      align: "center",
    },
    {
      name: "name",
      title: "案件名",
      type: "text",
      width: "200px",
      readOnly: true,
    },
    {
      name: "client",
      title: "取引先",
      type: "dropdown",
      source: clientOptionsFactory(clients),
      width: "250px",
      readOnly: true,
    },
    {
      name: "accDepartment",
      title: "計上部門",
      type: "dropdown",
      source: accDepartmentOptionsFactory(accDepartments),
      width: "140px",
      readOnly: true,
    },
    {
      name: "accSubject",
      title: "費用科目",
      type: "dropdown",
      source: accSubjectOptionsFactory(accSubjects),
      width: "140px",
      readOnly: true,
    },
    {
      name: "note",
      title: "備考",
      type: "text",
      width: "200px",
      readOnly: true,
    },
    {
      name: "available",
      title: "有効・無効",
      type: "dropdown",
      source: availableOptions,
      width: "140px",
      readOnly: true,
      align: "center",
    },
    {
      name: "created_at",
      title: "作成日",
      type: "hidden",
      width: "0",
      readOnly: true,
    },
    {
      name: "created_user",
      title: "作成ユーザー",
      type: "hidden",
      width: "0",
      readOnly: true,
    },
    {
      name: "updated_at",
      title: "更新日",
      type: "hidden",
      width: "0",
      readOnly: true,
    },
    {
      name: "updated_user",
      title: "更新ユーザー",
      type: "hidden",
      width: "0",
      readOnly: true,
    },
    {
      name: "id",
      title: "ドキュメントID",
      type: "hidden",
      width: "0",
      readOnly: true,
    },
    {
      name: "closing_year_month",
      title: "締め年月",
      type: "hidden",
      width: "0",
      readOnly: true,
    },
  ];
};

export default columnsOptions;
