import cuid from "cuid";
import lo from "lodash";
import firebase from "../config/firebase";

const db = firebase.firestore();
const COLLECTION_NAME = "client";

export function fetchClientsFromFirestore(yyyyMM) {
  return db
    .collection(COLLECTION_NAME)
    .where("closing_year_month", "==", yyyyMM)
    .orderBy("created_at", "asc");
}

export function loadFromClientFromFirestore(clientId) {
  return db.collection(COLLECTION_NAME).doc(clientId);
}

export function addClientToFirestore(client) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.FieldValue.serverTimestamp();
  return db
    .collection(COLLECTION_NAME)
    .doc(client.closing_year_month + "-" + cuid())
    .set({
      ...client,
      created_at: now,
      created_user: user.email,
      updated_at: now,
      updated_user: user.email,
    });
}

export function updateClientToFirestore(client) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.FieldValue.serverTimestamp();
  return db
    .collection(COLLECTION_NAME)
    .doc(client.id)
    .update({
      ...lo.omit(client, "id"),
      updated_at: now,
      updated_user: user.email,
    });
}
