import cuid from "cuid";
import lo from "lodash";
import firebase from "../config/firebase";

const db = firebase.firestore();
const COLLECTION_NAME = "project";

export function fetchProjectsFromFirestore(yyyyMM) {
  return db
    .collection(COLLECTION_NAME)
    .where("closing_year_month", "==", yyyyMM)
    .orderBy("created_at", "asc");
}

export function loadFromProjectFromFirestore(projectId) {
  return db.collection(COLLECTION_NAME).doc(projectId);
}

export function addProjectToFirestore(project) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.FieldValue.serverTimestamp();
  return db
    .collection(COLLECTION_NAME)
    .doc(project.closing_year_month + "-" + cuid())
    .set({
      ...project,
      created_at: now,
      created_user: user.email,
      updated_at: now,
      updated_user: user.email,
    });
}

export function updateProjectToFirestore(project) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.FieldValue.serverTimestamp();
  return db
    .collection(COLLECTION_NAME)
    .doc(project.id)
    .update({
      ...lo.omit(project, "id"),
      updated_at: now,
      updated_user: user.email,
    });
}
