import {
  asyncActionError,
  asyncActionFinish,
  asyncActionStart,
} from "../../../app/async/asyncReducer";
import { dataFromSnapshot } from "../../../app/firestore/firestoreService";
import {
  addMemberAffiliationToFirestore,
  fetchMemberAffiliationsFromFirestore,
  updateMemberAffiliationToFirestore,
} from "../../../app/firestore/memberAffiliationService";

export const MEMBERAFFILIATION_CREATE = "memberAffiliation/create";
export const MEMBERAFFILIATION_UPDATE = "memberAffiliation/update";
export const MEMBERAFFILIATIONS_FETCH = "memberAffiliation/fetch";
export const MEMBERAFFILIATION_LISTEN = "memberAffiliation/listen";
export const MEMBERAFFILIATION_SELECTED = "memberAffiliation/selected";

export function selectMemberAffiliation(memberAffiliation) {
  return {
    type: MEMBERAFFILIATION_SELECTED,
    payload: memberAffiliation,
  };
}

export function createMemberAffiliation(memberAffiliation) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await addMemberAffiliationToFirestore(memberAffiliation);
      dispatch({
        type: MEMBERAFFILIATION_CREATE,
        payload: memberAffiliation,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function updateMemberAffiliation(memberAffiliation) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      await updateMemberAffiliationToFirestore(memberAffiliation);
      dispatch({
        type: MEMBERAFFILIATION_UPDATE,
        payload: memberAffiliation,
      });
      dispatch(asyncActionFinish());
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

export function fetchMemberAffiliations(yyyyMM) {
  return async function (dispatch) {
    try {
      dispatch(asyncActionStart());
      const snapshot = fetchMemberAffiliationsFromFirestore(yyyyMM).get();
      const data = (await snapshot).docs.map((doc) => {
        const ret = dataFromSnapshot(doc);
        const href = `<a href="#${ret.id}">編集</a>`;
        return { href, ...ret };
      });
      dispatch({
        type: MEMBERAFFILIATIONS_FETCH,
        payload: data,
      });
      dispatch(asyncActionFinish());
      return data;
    } catch (error) {
      dispatch(asyncActionError(error));
    }
  };
}

const initialState = {
  memberAffiliations: [],
  selectedMemberAffiliation: null,
};

export default function memberAffiliationReducer(
  state = initialState,
  { type, payload }
) {
  switch (type) {
    case MEMBERAFFILIATION_SELECTED:
      return {
        ...state,
        selectedMemberAffiliation: payload,
      };
    case MEMBERAFFILIATION_CREATE:
      return state;
    case MEMBERAFFILIATION_UPDATE:
      return state;
    case MEMBERAFFILIATIONS_FETCH:
      return {
        ...state,
        memberAffiliations: payload,
      };
    case MEMBERAFFILIATION_LISTEN:
      return {
        ...state,
        selectedMemberAffiliation: payload,
      };
    default:
      return state;
  }
}
