import React, { useState } from "react";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Redirect } from "react-router-dom";
import { Icon, Menu, Sidebar } from "semantic-ui-react";
import NowLoading from "../../../app/common/utils/NowLoading";
import { fetchMemberTags } from "../memberTags/memberTagReducer";
import ClientForm from "./ClientForm";
import ClientList from "./ClientList";
import { fetchClients } from "./clientReducer";

export default function ClientDashboard({ location }) {
  const clientId = location.hash?.replace("#", "");
  const [visibleDetail, setVisibleDetail] = useState(false);
  const { currentPeriod } = useSelector((state) => state.period);
  const dispatch = useDispatch();
  const [refresh, setRefresh] = useState(0);
  const { loading, error } = useSelector((state) => state.async);

  useEffect(() => {
    if (clientId) {
      setVisibleDetail(true);
    }
  }, [clientId]);

  useEffect(() => {
    dispatch(fetchMemberTags(currentPeriod.yearMonth));
    dispatch(fetchClients(currentPeriod.yearMonth));
  }, [currentPeriod.yearMonth, dispatch, refresh]);

  const handleRefresh = () => {
    setRefresh(refresh + 1);
  };

  if (loading && !visibleDetail) {
    return <NowLoading />;
  }

  if (error) {
    return <Redirect to="/error" />;
  }

  return (
    <>
      <Sidebar.Pushable>
        <Sidebar
          width="wide"
          as={Menu}
          animation="overlay"
          direction="right"
          vertical
          visible={visibleDetail}>
          <Menu.Item>
            <ClientForm
              clientId={clientId}
              setVisibleDetail={setVisibleDetail}
              handleRefresh={handleRefresh}
            />
          </Menu.Item>
        </Sidebar>

        <Sidebar.Pusher className="jpusher" dimmed={visibleDetail}>
          <Menu secondary className="jmenu">
            <Menu.Item className={"secondary_toolbar_title"}>取引先</Menu.Item>
            <Menu.Item
              onClick={() => {
                handleRefresh();
              }}>
              <Icon name="refresh" />
              最新の状態に更新
            </Menu.Item>
            <Menu.Item onClick={() => setVisibleDetail(!visibleDetail)}>
              <Icon name="edit" />
              新しいレコード
            </Menu.Item>
          </Menu>
          <ClientList refresh={refresh} />
        </Sidebar.Pusher>
      </Sidebar.Pushable>
    </>
  );
}
