import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, Redirect, useHistory } from "react-router-dom";
import { Button, Menu } from "semantic-ui-react";
import { toast } from "react-toastify";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import MyTextInput from "../../../app/common/form/MyTextInput";
import MySelectInput from "../../../app/common/form/MySelectInput";
import { availableOptions } from "../../../app/options/availableOptions";
import NowLoading from "../../../app/common/utils/NowLoading";
import useFirestoreDoc from "../../../app/hooks/useFirestoreDoc";
import { loadFromMemberAffiliationFromFirestore } from "../../../app/firestore/memberAffiliationService";
import {
  createMemberAffiliation,
  selectMemberAffiliation,
  updateMemberAffiliation,
} from "./memberAffiliationReducer";
import { memberTagOptionsFactory } from "../../../app/options/memberTagOptionsFactory";

export default function MemberAffiliationForm({
  memberAffiliationId,
  setVisibleDetail,
  handleRefresh,
}) {
  const { memberTags } = useSelector((state) => state.memberTag);
  const { currentPeriod } = useSelector((state) => state.period);
  const { selectedMemberAffiliation } = useSelector(
    (state) => state.memberAffiliation
  );
  const { loading, error } = useSelector((state) => state.async);
  const dispatch = useDispatch();
  const history = useHistory();

  const initialValues = selectedMemberAffiliation ?? {
    closing_year_month: currentPeriod.yearMonth,
    name: "",
    tag: "true",
    available: "true",
  };

  const validationSchema = Yup.object({
    name: Yup.string().required("拠点名称を入力してください"),
    tag: Yup.string().required("メンバータグを設定してください"),
    available: Yup.string().required("有効・無効を設定してください"),
  });

  useFirestoreDoc({
    shouldExecute: !!memberAffiliationId,
    query: () => loadFromMemberAffiliationFromFirestore(memberAffiliationId),
    data: (item) => dispatch(selectMemberAffiliation(item)),
    deps: [memberAffiliationId, dispatch],
  });

  if (loading) {
    return <NowLoading />;
  }

  if (error) {
    return <Redirect to="/error" />;
  }

  return (
    <>
      <Menu secondary className="jmenu">
        <Menu.Item className={"secondary_toolbar_title"}>
          メンバー所属
        </Menu.Item>
      </Menu>
      <Formik
        enableReinitialize={true}
        validateOnBlur={false}
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={async (values, { setSubmitting }) => {
          try {
            setSubmitting(true);
            memberAffiliationId
              ? dispatch(updateMemberAffiliation(values))
              : dispatch(createMemberAffiliation(values));
            setVisibleDetail(false);
            history.push("/memberaffiliations");
            handleRefresh();
          } catch (error) {
            toast.error(error.message);
          } finally {
            setSubmitting(false);
          }
        }}>
        {({ isSubmitting, dirty, isValid, resetForm }) => (
          <Form className="ui form">
            <input type="hidden" name="id" />
            <input type="hidden" name="closing_year_month" />
            <MyTextInput label="所属名称" name="name" placeholder="所属名称" />
            <MySelectInput
              label="メンバータグ"
              name="tag"
              options={memberTagOptionsFactory(memberTags)}
            />
            <MySelectInput
              label="有効・無効"
              name="available"
              options={availableOptions}
            />

            <Button
              loading={isSubmitting}
              disabled={!isValid || !dirty || isSubmitting}
              type="submit"
              positive
              content="保存"
            />
            <Button
              negative
              content="キャンセル"
              as={Link}
              to="/memberaffiliations"
              onClick={() => {
                setVisibleDetail(false);
                resetForm();
              }}
            />
          </Form>
        )}
      </Formik>
    </>
  );
}
