import React, { useRef } from "react";
import { useSelector } from "react-redux";
import SpreadSheet from "../../../app/common/form/SpreadSheet";
import columnsOptions from "./columnsOptions";

export default function ProjectList({ refresh }) {
  const { clients } = useSelector((state) => state.client);
  const { accDepartments } = useSelector((state) => state.accDepartment);
  const { accSubjects } = useSelector((state) => state.accSubject);
  const { projects } = useSelector((state) => state.project);

  const options = useRef({
    columns: columnsOptions(clients, accDepartments, accSubjects),
  });

  return <SpreadSheet options={options} data={projects} />;
}
