export const memberAffiliationOptionsFactory = (
  memberAffiliations,
  available = "true"
) => {
  return memberAffiliations
    .filter((affiliation) => affiliation.available === available)
    .map((affiliation) => ({
      key: affiliation.id,
      id: affiliation.id,
      text: affiliation.name,
      name: affiliation.name,
      value: affiliation.id,
    }));
};
