import React, { useState } from "react";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Redirect } from "react-router-dom";
import { Icon, Menu, Sidebar } from "semantic-ui-react";
import NowLoading from "../../../app/common/utils/NowLoading";
import { fetchMemberHubs } from "../memberHubs/memberHubReducer";
import { fetchMemberRanks } from "../memberRanks/memberRankReducer";
import MemberHubCostSalesForm from "./MemberHubCostSalesForm";
import MemberHubCostSalesList from "./MemberHubCostSalesList";
import { fetchMemberHubCostSaless } from "./memberHubCostSalesReducer";

export default function MemberHubCostSalesDashboard({ location }) {
  const memberHubCostSalesId = location.hash?.replace("#", "");
  const [visibleDetail, setVisibleDetail] = useState(false);
  const { currentPeriod } = useSelector((state) => state.period);
  const dispatch = useDispatch();
  const [refresh, setRefresh] = useState(0);
  const { loading, error } = useSelector((state) => state.async);

  useEffect(() => {
    if (memberHubCostSalesId) {
      setVisibleDetail(true);
    }
  }, [memberHubCostSalesId]);

  useEffect(() => {
    dispatch(fetchMemberHubs(currentPeriod.yearMonth));
    dispatch(fetchMemberRanks(currentPeriod.yearMonth));
    dispatch(fetchMemberHubCostSaless(currentPeriod.yearMonth));
  }, [currentPeriod.yearMonth, dispatch, refresh]);

  const handleRefresh = () => {
    setRefresh(refresh + 1);
  };

  if (loading && !visibleDetail) {
    return <NowLoading />;
  }

  if (error) {
    return <Redirect to="/error" />;
  }

  return (
    <>
      <Sidebar.Pushable>
        <Sidebar
          width="wide"
          as={Menu}
          animation="overlay"
          direction="right"
          vertical
          visible={visibleDetail}>
          <Menu.Item>
            <MemberHubCostSalesForm
              memberHubCostSalesId={memberHubCostSalesId}
              setVisibleDetail={setVisibleDetail}
              handleRefresh={handleRefresh}
            />
          </Menu.Item>
        </Sidebar>

        <Sidebar.Pusher className="jpusher" dimmed={visibleDetail}>
          <Menu secondary className="jmenu">
            <Menu.Item className={"secondary_toolbar_title"}>
              メンバー拠点別標準原価売上表
            </Menu.Item>
            <Menu.Item
              onClick={() => {
                handleRefresh();
              }}>
              <Icon name="refresh" />
              最新の状態に更新
            </Menu.Item>
            <Menu.Item onClick={() => setVisibleDetail(!visibleDetail)}>
              <Icon name="edit" />
              新しいレコード
            </Menu.Item>
          </Menu>
          <MemberHubCostSalesList refresh={refresh} />
        </Sidebar.Pusher>
      </Sidebar.Pushable>
    </>
  );
}
