import React from "react";
import { Link } from "react-router-dom";
import { Dropdown, Menu } from "semantic-ui-react";

export default function MasterMenu() {
  return (
    <Menu.Item>
      <Dropdown pointing="top right" text="設定">
        <Dropdown.Menu>
          <Dropdown.Item as={Link} to="/members" text="メンバー" icon="users" />
          <Dropdown.Item
            as={Link}
            to="/memberaffiliations"
            text="メンバー所属"
            icon="users"
          />
          <Dropdown.Item
            as={Link}
            to="/membertags"
            text="メンバータグ"
            icon="users"
          />
          <Dropdown.Item
            as={Link}
            to="/memberranks"
            text="メンバーランク"
            icon="users"
          />
          <Dropdown.Item
            as={Link}
            to="/memberhubs"
            text="メンバー拠点（標準売上原価用）"
            icon="users"
          />
          <Dropdown.Item
            as={Link}
            to="/memberhubcostsaless"
            text="メンバー拠点別標準原価売上表"
            icon="users"
          />
          <Dropdown.Divider />
          <Dropdown.Item
            as={Link}
            to="/clients"
            text="取引先"
            icon="building outline"
          />
          <Dropdown.Divider />
          <Dropdown.Item as={Link} to="/projects" text="案件" icon="inbox" />
          <Dropdown.Item
            as={Link}
            to="/accdepartments"
            text="計上部門"
            icon="chart bar"
          />
          <Dropdown.Item
            as={Link}
            to="/accsubjects"
            text="費用科目"
            icon="chart bar"
          />
        </Dropdown.Menu>
      </Dropdown>
    </Menu.Item>
  );
}
