import cuid from "cuid";
import lo from "lodash";
import firebase from "../config/firebase";

const db = firebase.firestore();
const COLLECTION_NAME = "acc_department";

export function fetchAccDepartmentsFromFirestore(yyyyMM) {
  return db
    .collection(COLLECTION_NAME)
    .where("closing_year_month", "==", yyyyMM)
    .orderBy("created_at", "asc");
}

export function loadFromAccDepartmentFromFirestore(accDepartmentId) {
  return db.collection(COLLECTION_NAME).doc(accDepartmentId);
}

export function addAccDepartmentToFirestore(accDepartment) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.FieldValue.serverTimestamp();
  return db
    .collection(COLLECTION_NAME)
    .doc(accDepartment.closing_year_month + "-" + cuid())
    .set({
      ...accDepartment,
      created_at: now,
      created_user: user.email,
      updated_at: now,
      updated_user: user.email,
    });
}

export function updateAccDepartmentToFirestore(accDepartment) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.FieldValue.serverTimestamp();
  return db
    .collection(COLLECTION_NAME)
    .doc(accDepartment.id)
    .update({
      ...lo.omit(accDepartment, "id"),
      updated_at: now,
      updated_user: user.email,
    });
}
