export const PERIOD_LATEST = "period/latest";
export const PERIOD_CHANGE_CURRENT = "period/change_current";
export const PERIOD_CREATE_START = "period/create_start";
export const PERIOD_CREATE_FINISHED = "period/create_finished";
export const PERIOD_REMOVE = "period/remove";
export const PERIOD_CLOSE = "period/close";
export const PERIOD_OPEN = "period/opeb";

/** 締め情報を読み込んで、最後の締め情報を設定する */
export function latestPeriod() {
  return function (dispatch) {
    // datastore にアクセスして締め情報リストと最後の締め情報を設定する
    const latest = [
      { yearMonth: 202011, close: false },
      { yearMonth: 202010, close: false },
      { yearMonth: 202009, close: true },
      { yearMonth: 202008, close: true },
      { yearMonth: 202007, close: true },
      { yearMonth: 202006, close: true },
      { yearMonth: 202005, close: true },
    ];

    dispatch({ type: PERIOD_LATEST, payload: latest });
  };
}

/** 現在の締め年月を変更する */
export function changeCurrentPeriod(yearMonth) {
  return {
    type: PERIOD_CHANGE_CURRENT,
    payload: yearMonth,
  };
}

/** 指定した締め情報を作成する（開始） */
export function createPeriodStart(yearMonth) {
  return {
    type: PERIOD_CREATE_START,
    payload: yearMonth,
  };
}

/** 指定した締め情報を作成する（終了） */
export function createPeriodFinished() {
  return {
    type: PERIOD_CREATE_FINISHED,
  };
}

/** 指定した締め情報を削除する */
export function removePeriod(yearMonth) {
  return {
    type: PERIOD_REMOVE,
    payload: yearMonth,
  };
}

/** 指定した締め年月を閉じる */
export function closePeriod(yearMonth) {
  return {
    type: PERIOD_CLOSE,
    payload: yearMonth,
  };
}

/** 指定した締め年月を開く */
export function openPeriod(yearMonth) {
  return {
    type: PERIOD_OPEN,
    payload: yearMonth,
  };
}

const initialState = {
  creating: false,
  periods: [],
  currentPeriod: null,
  isOpen: false,
};

export default function periodReducer(state = initialState, { type, payload }) {
  switch (type) {
    case PERIOD_LATEST:
      return {
        ...state,
        periods: payload,
        currentPeriod: payload.length === 0 ? null : payload[0],
      };
    case PERIOD_CHANGE_CURRENT:
      return {
        ...state,
        currentPeriod: state.periods.filter((n) => n.yearMonth === payload)[0],
      };
    default:
      return state;
  }
}
