import React from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  Button,
  Divider,
  Dropdown,
  Grid,
  Header,
  Icon,
  Input,
  Menu,
  Segment,
  Select,
} from "semantic-ui-react";
import { displayYYYYMMJp } from "../../app/common/utils/textFormstUtils";
import { changeCurrentPeriod } from "../period/periodReducer";

export default function Dashboard() {
  const { currentPeriod, periods } = useSelector((state) => state.period);
  const dispatch = useDispatch();
  const monthOptions = [
    { key: "1", text: "1", value: "01" },
    { key: "2", text: "2", value: "02" },
    { key: "3", text: "3", value: "03" },
    { key: "4", text: "4", value: "04" },
    { key: "5", text: "5", value: "05" },
    { key: "6", text: "6", value: "06" },
    { key: "7", text: "7", value: "07" },
    { key: "8", text: "8", value: "08" },
    { key: "9", text: "9", value: "09" },
    { key: "10", text: "10", value: "10" },
    { key: "11", text: "11", value: "11" },
    { key: "12", text: "12", value: "12" },
  ];

  return (
    <>
      <Menu secondary className="jmenu">
        <Menu.Item className={"secondary_toolbar_title"}>DashBoard</Menu.Item>
      </Menu>
      <Segment.Group>
        <Segment>
          <Header as="h3">締め年月</Header>
        </Segment>
        <Segment>
          <Grid columns={2} textAlign="center">
            <Grid.Column>
              <p> 編集を行う締め年月を選択してください。</p>
              <Dropdown
                pointing="top left"
                text={displayYYYYMMJp(currentPeriod.yearMonth)}>
                <Dropdown.Menu>
                  {periods
                    .filter((n) => n.yearMonth !== currentPeriod.yearMonth)
                    .map((period) => (
                      <Dropdown.Item
                        key={period.yearMonth}
                        onClick={(e) => {
                          dispatch(changeCurrentPeriod(period.yearMonth));
                        }}>
                        <Icon name={period.close ? "lock" : "clock outline"} />
                        {displayYYYYMMJp(period.yearMonth)}
                      </Dropdown.Item>
                    ))}
                </Dropdown.Menu>
              </Dropdown>
            </Grid.Column>
            <Grid.Column>
              <p>新しい年月を新規作成してください。</p>
              <Input type="text">
                <input value="2020" style={{ width: "5em" }} />
                <Select compact options={monthOptions} defaultValue="12" />
                <Button color="teal">新規作成</Button>
              </Input>
            </Grid.Column>
          </Grid>
          <Divider vertical>Or</Divider>
        </Segment>
      </Segment.Group>
    </>
  );
}
