import cuid from "cuid";
import lo from "lodash";
import firebase from "../config/firebase";

const db = firebase.firestore();
const COLLECTION_NAME = "member_tag";

export function fetchMemberTagsFromFirestore(yyyyMM) {
  return db
    .collection(COLLECTION_NAME)
    .where("closing_year_month", "==", yyyyMM)
    .orderBy("created_at", "asc");
}

export function loadFromMemberTagFromFirestore(memberTagId) {
  return db.collection(COLLECTION_NAME).doc(memberTagId);
}

export function addMemberTagToFirestore(memberTag) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.FieldValue.serverTimestamp();
  return db
    .collection(COLLECTION_NAME)
    .doc(memberTag.closing_year_month + "-" + cuid())
    .set({
      ...memberTag,
      created_at: now,
      created_user: user.email,
      updated_at: now,
      updated_user: user.email,
    });
}

export function updateMemberTagToFirestore(memberTag) {
  const user = firebase.auth().currentUser;
  const now = firebase.firestore.FieldValue.serverTimestamp();
  return db
    .collection(COLLECTION_NAME)
    .doc(memberTag.id)
    .update({
      ...lo.omit(memberTag, "id"),
      updated_at: now,
      updated_user: user.email,
    });
}
