import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, Redirect, useHistory } from "react-router-dom";
import { Button, Menu } from "semantic-ui-react";
import { toast } from "react-toastify";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import MyTextInput from "../../../app/common/form/MyTextInput";
import MySelectInput from "../../../app/common/form/MySelectInput";
import { useCostSalesOptions } from "../../../app/options/useCostSalesOptions";
import { availableOptions } from "../../../app/options/availableOptions";
import NowLoading from "../../../app/common/utils/NowLoading";
import useFirestoreDoc from "../../../app/hooks/useFirestoreDoc";
import { loadFromMemberTagFromFirestore } from "../../../app/firestore/memberTagService";
import {
  createMemberTag,
  selectMemberTag,
  updateMemberTag,
} from "./memberTagReducer";

export default function MemberTagForm({
  memberTagId,
  setVisibleDetail,
  handleRefresh,
}) {
  const { currentPeriod } = useSelector((state) => state.period);
  const { selectedMemberTag } = useSelector((state) => state.memberTag);
  const { loading, error } = useSelector((state) => state.async);
  const dispatch = useDispatch();
  const history = useHistory();

  const initialValues = selectedMemberTag ?? {
    closing_year_month: currentPeriod.yearMonth,
    name: "",
    use_cost_sales: "true",
    available: "true",
  };

  const validationSchema = Yup.object({
    closing_year_month: Yup.string().required("締め年月を入力してください"),
    name: Yup.string().required("名称を入力してください"),
    use_cost_sales: Yup.string().required(
      "「標準売上原価利用可否」を設定してください"
    ),
    available: Yup.string().required("有効・無効を設定してください"),
  });

  useFirestoreDoc({
    shouldExecute: !!memberTagId,
    query: () => loadFromMemberTagFromFirestore(memberTagId),
    data: (item) => dispatch(selectMemberTag(item)),
    deps: [memberTagId, dispatch],
  });

  if (loading) {
    return <NowLoading />;
  }

  if (error) {
    return <Redirect to="/error" />;
  }

  return (
    <>
      <Menu secondary className="jmenu">
        <Menu.Item className={"secondary_toolbar_title"}>
          メンバータグ
        </Menu.Item>
      </Menu>
      <Formik
        enableReinitialize={true}
        validateOnBlur={false}
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={async (values, { setSubmitting }) => {
          try {
            setSubmitting(true);
            memberTagId
              ? dispatch(updateMemberTag(values))
              : dispatch(createMemberTag(values));
            setVisibleDetail(false);
            history.push("/membertags");
            handleRefresh();
          } catch (error) {
            toast.error(error.message);
          } finally {
            setSubmitting(false);
          }
        }}>
        {({ isSubmitting, dirty, isValid, resetForm }) => (
          <Form className="ui form">
            <input type="hidden" name="id" />
            <input type="hidden" name="closing_year_month" />
            <MyTextInput label="タグ名称" name="name" placeholder="タグ名称" />
            <MySelectInput
              label="標準売上原価利用可否"
              name="use_cost_sales"
              options={useCostSalesOptions}
            />
            <MySelectInput
              label="有効・無効"
              name="available"
              options={availableOptions}
            />

            <Button
              loading={isSubmitting}
              disabled={!isValid || !dirty || isSubmitting}
              type="submit"
              positive
              content="保存"
            />
            <Button
              negative
              content="キャンセル"
              as={Link}
              to="/membertags"
              onClick={() => {
                setVisibleDetail(false);
                resetForm();
              }}
            />
          </Form>
        )}
      </Formik>
    </>
  );
}
