import React, { useEffect, useRef, useState } from "react";
import { Icon, Input, Menu, Sidebar } from "semantic-ui-react";
import jexcel from "jexcel";
import "jexcel/dist/jexcel.css";
import columnsOptions from "./columnsOptions";
import data from "./assignData";

const options = {
  data: data,
  columns: columnsOptions,
  filters: true,
  allowComments: true,
  contextMenu: function (obj, x, y, e) {
    const items = [];

    if (x && y) {
      items.push({
        title: "編集",
        onclick: function () {
          alert("選択CELL：" + x + ":" + y);
        },
      });
    }

    return items;
  },
};

export default function AssignPage() {
  const [visible, setVisible] = useState(false);

  const jexcelRef = useRef(null);
  useEffect(() => {
    jexcel(jexcelRef.current, options);
  }, []);

  return (
    <>
      <Sidebar.Pushable>
        <Sidebar
          as={Menu}
          animation="push"
          visible={visible}
          onHide={() => setVisible(false)}
          vertical
          direction="left">
          <Menu.Item>
            <Input type="text" icon="filter" />
          </Menu.Item>
        </Sidebar>
        <Sidebar.Pusher className={"jpusher"}>
          <Menu secondary className="jmenu">
            <Menu.Item className={"secondary_toolbar_title"}>Assign</Menu.Item>
            <Menu.Item
              active={visible}
              onClick={() => setVisible(!visible)}
              color="green">
              <Icon name="filter" />
              フィルター
            </Menu.Item>
            <Menu.Item
              onClick={() => {
                alert("save");
              }}>
              <Icon name="save" />
              保存/最新の状態に更新
            </Menu.Item>
          </Menu>
          <div ref={jexcelRef} />
        </Sidebar.Pusher>
      </Sidebar.Pushable>
    </>
  );
}
